html, .App {
  background-color: #eae7dc;
}

.App {
  height: 100vh;
}

body {
  margin: 0px;
  padding: 0px;
}

* {
    font-family: -webkit-pictograph;
}

.item {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    background-color: whitesmoke;
    color:black;
    border: 2px solid black;
    padding: 10px;
    border-radius: 7px;
    align-items: flex-start;
}

.item.dragging {
    background-color: lightgray ;
}

.item:hover {
    background-color: white;
}

.cardTop {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.task {
    font-size: 1.2em;
    font-weight: 500;
}

.cardBottom {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-self: flex-end;
}

.category {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
    border-radius: 7px;
    text-align: center;
    color: black;
    width: 33%;
    height: 25px;
}

.usercontainer {
    display:flex;
    position: relative;
}

.username {
    padding-right: 10px;
    font-weight: 600;
}

.portrait-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    border-radius: 50%;
    padding: 7px;
    height: 50px;
    width: 50px;
}


.portrait {
    height: 30px;
}

.high {
    background-color: red;
  }
  
  .medium {
    background-color: orange;
  }
  
  .low {
    background-color: green;
  }
  






h3 {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #8e8d8a;
    background-color: #d8c3a5;
    border-radius: 7px 7px 0 0;
    font-size: x-large;
    font-family: -webkit-pictograph;
    font-weight: 900;
    text-align: center;
    margin-bottom: 0px;
}

.column-todo,
.column-in-progress,
.column-done
{
    width: 30%;
}

.droppable-col-todo, 
.droppable-col-in-progress, 
.droppable-col-done {
    height: auto;
    min-height: 150px;
    background-color: #d8c3a5;
    padding: 10px 10px 0 10px;
    border-radius: 0 0 7px 7px;
    display:flex;
    flex-direction: column;
}

.sections {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    height: 100%;
}

.high, .medium, .low {
    border-radius: 7px;
    height: 15px;
    width: 20%;
}
.Modal *{
    font-family: -webkit-pictograph;
}

.Modal {
    background-color: #EAE7DC;
    border-radius: 2px;
    margin: 48px 0 80px;
    width: 500px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    outline: none;
    padding: 20px;
}

.Overlay {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);;
}

.modalcontainer {
    background-color: #fff;
    padding: 20px;
}

.headcontainermodal {
    display:flex;
    justify-content: space-between;
}

.modaltitle {
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.modalicon {
    height: 20px;
    width: 20px;
}

.close-btn-ctn {
    display: flex;
}

.close-btn {
    font-size: 20px;
    color: #031D2C;
    border: none;
    border-radius: 25px;
    background-color:#EAE7DC;
    height: 30px;
    width: 30px;
}

.close-btn:hover {
    background-color: #8E8D89;
}

.form-col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}
  
.form-col-75 {
  float: left;
  width: 75%;
  margin-top: 6px;
}
  
  /* Clear floats after the columns */
  .form-row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 600px) {
    .form-col-25, form-.col-75 {
      width: 100%;
      margin-top: 0;
    }
}

.Modal input[type=text] {
    width: 100%;
