    border-radius: 4px;
    background-color: #EAE7DC;
    height: 28.8px;
  }

  .Modal input#category:focus,
  input:focus {
    background-color: #d9b08c;
    outline-color: #8e8d8a;
  }

  .form-container-icon{
      position:relative;
      display:flex;
