@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

h3 {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #8e8d8a;
    background-color: #d8c3a5;
    border-radius: 7px 7px 0 0;
    font-size: x-large;
    font-family: -webkit-pictograph;
    font-weight: 900;
    text-align: center;
    margin-bottom: 0px;
}

.column-todo,
.column-in-progress,
.column-done
{
    width: 30%;
}

.droppable-col-todo, 
.droppable-col-in-progress, 
.droppable-col-done {
    height: auto;
    min-height: 150px;
    background-color: #d8c3a5;
    padding: 10px 10px 0 10px;
    border-radius: 0 0 7px 7px;
    display:flex;
    flex-direction: column;
}

.sections {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    height: 100%;
}

.high, .medium, .low {
    border-radius: 7px;
    height: 15px;
    width: 20%;
}